/** @format */
import React, { useState } from "react";

const MyAccordian = ({ question, answer }) => {
  const [show, dShow] = useState(false);
  return (
    <>
      <div className="main">
        <div className="center">
          <p
            onClick={() => {
              dShow(!show);
            }}
          >
            {show ? "-" : "+"}
          </p>
          <h1>{question}</h1>
          <p> {show && answer}</p>
        </div>
      </div>
    </>
  );
};

export default MyAccordian;
